



































import { Component, Vue } from 'vue-property-decorator';
import Jsbarcode from 'jsbarcode';
import { State } from 'vuex-class';
import MainFooter from '../../components/MainFooter.vue';

@Component({
  components: {
    MainFooter,
  },
})

export default class MainPage extends Vue {
  @State('name') stateName !: string;

  editProfile() {
    this.$router.push('/member/edit_profile');
  }

  editSubscribe() {
    this.$router.push('/member/edit_subscribe');
  }

  showRecord() {
    this.$router.push('/member/test_record');
  }

  interestExam() {
    this.$router.push({ path: '/member/interest_exam', query: { process: 'member' } });
  }

  mounted() {
    Jsbarcode('#barcode', '12345678', {
      width: 2,
      height: 50,
      font: 'Noto Sans CJK TC',
      fontSize: 16,
    });
  }
}
